





























	import BASEURL from "../../common/baseInfo.js";
	import graceChecker from "../../common/graceChecker.js";
	export default {
		data() {
			return {
				openId: '',
				userInfo:{},
				userPhone: '',
				phoneVal: null,
				countdown: null,
				codeVal: null
			};
		},
		onLoad() {
			let that = this;
			uni.getStorage({
				key: 'userInfo',
				success: function (res) {
					// console.log(JSON.parse(res));
					console.log(res.data);
					that.userInfo = res.data;
				}
			})
			uni.getStorage({
				key: 'openIdInfo',
				success: function (res) {
					// console.log(JSON.parse(res));
					console.log(res);
					that.openId = res.data;
				}
			})
		},
		methods:{
			// 点击获取验证码
			getCheckTextFn:function () {
				console.log(111);
				var that = this,
					regphone = /^(13\d|14[57]|15[012356789]|18\d|17[013678])\d{8}$/;
				  //进行表单检查
				  console.log(that.phoneVal);
				  if(!regphone.test(that.phoneVal)) {
					  uni.showToast({title:"请输入正确的手机号", icon:"none"});
					  return;
				  }
// 				  var sn = 60;
// 				  var timer = setInterval(function(){
// 				  	sn--;
// 				  	that.countdown = '重新发送'+'('+sn+')';
// 				  	if(sn == 0){
// 				  		clearInterval(timer);
// 				  		that.countdown = null;
// 				  	}
// 				  },1000);
				  uni.request({
				  	url: `${BASEURL}/khxcx-0.0.1-SNAPSHOT/getSmsNumber.action`,
				  	method: 'GET',
				  	data: {
						"phoneNumber":that.phoneVal
					},
				  	success: res => {
						console.log(res);
						//服务器返回响应，根据响应结果，分析是否登录成功；
						var sn = 60;
						var timer = setInterval(function(){
							sn--;
							that.countdown = '重新发送'+'('+sn+')';
							if(sn == 0){
								clearInterval(timer);
								that.countdown = null;
							}
						},1000);
					},
				  	fail: (err) => {
						console.log(err);
					},
				  	complete: () => {}
				  });
			},
			submitForm() {
				var that = this,
					regphone = /^(13\d|14[57]|15[012356789]|18\d|17[013678])\d{8}$/;
				  //进行表单检查
				  console.log(that.phoneVal);
				  if(!regphone.test(that.phoneVal)) {
					  uni.showToast({title:"请输入正确的手机号", icon:"none"});
					  return;
				  }
				  if (that.codeVal == null || that.codeVal.length != 4) {
					  uni.showToast({title:"请输入正确的验证码", icon:"none"});
					  return;
				  }
				  uni.request({
				  	url: `${BASEURL}/khxcx-0.0.1-SNAPSHOT/verifyByMobileNumber.action`,
				  	method: 'GET',
				  	data: {
						openId: that.openId,
						phoneNumber:that.phoneVal,
						code:that.codeVal
						},
				  	success: res => {
						console.log(res);
						uni.showToast({
							title: '验证成功',
							icon: 'success',
							mask: true,
							duration: 2000
						});
						setTimeout(() => {
							uni.navigateBack();
						},2000)
					},
				  	fail: (err) => {
						console.log(err);
					},
				  	complete: () => {}
				  });
			}
		}
	}
